.ChargesForm {
  margin-bottom: 10px;

  .basicForm1 {
    border: 1px solid;
    border-color: #d2d3d4;
    width: fit-content;
    border-top-right-radius: 16px;
    border-top-left-radius: 16px;
  }
  .basicForm {
    border: 1px solid;
    border-color: #d2d3d4;
    width: fit-content;
    border-radius: 16px;
  }
  table {
    width: 100%;
    border-collapse: collapse;
    overflow: hidden;
    position: relative;

    th {
      min-width: 226px;
      padding: 14px;
      background: #e2f8fb;
      position: relative;
      z-index: 1;
      color: #2a5067;
      font-family: Inter;
      font-size: 14px;
      font-style: normal;
      font-weight: 500;
      line-height: 20px;

      &:first-child {
        border-top-left-radius: 16px;
      }

      &:last-child {
        border-top-right-radius: 16px;
      }

      &.rightBorder {
        border-right: 1px solid #d2d3d4;
      }
    }

    th:nth-child(2) {
      min-width: 226px;
    }
    .rightBottomRadius {
      border-bottom-right-radius: 16px;
    }
    .leftBottomRadius {
      border-bottom-left-radius: 16px;
    }
    

    .odd {
      background-color: #fff;
    }
    .even {
      background-color: #fafbfb;
    }

    .error {
      position: absolute;
      bottom: 5%;
    }

    td {
      position: relative;
      min-width: 160px;
      padding: 13px 14px;
      height: 45px;
      border: none;

      &.rightBorder {
        border-right: 1px solid #d2d3d4 !important;
      }

      .comments {
        color: #1e2528;
        display: inline-block;
        width: 310px;
        font-family: Inter;
        font-size: 12px;
        font-style: normal;
        font-weight: 400;
        line-height: 18px;
      }
    }

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      box-sizing: border-box;
      border: 0px solid #d2d3d4;
      border-radius: 16px !important;
      z-index: 0;
    }
  }

  .newChargeType {
    color: #935ed6;
    margin: 8px;
    font-size: 13px;
    font-weight: 500;
  }

  hr {
    border-top: 2px solid #e8e9e9;
  }

  .btn {
    column-gap: 17px;
  }
  .test:nth-child(even) {
    background: red;
    color: #2cdede;
  }
}
